@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Regular.ttf") format("truetype"); }

@font-face {
  font-family: 'Roboto-Thin';
  src: url("../fonts/Roboto/Roboto-Thin.ttf") format("truetype"); }

@font-face {
  font-family: 'Roboto-Bold';
  src: url("../fonts/Roboto/Roboto-Bold.ttf") format("truetype"); }

@font-face {
  font-family: 'OpenSans';
  src: url("../fonts/Open_Sans/OpenSans-Regular.ttf") format("truetype"); }

@font-face {
  font-family: 'OpenSans-Bold';
  src: url("../fonts/Open_Sans/OpenSans-Bold.ttf") format("truetype"); }

@font-face {
  font-family: 'Oswald';
  src: url("../fonts/Oswald/Oswald-Regular.ttf") format("truetype"); }

@font-face {
  font-family: 'Dosis';
  src: url("../fonts/Dosis/Dosis-Regular.ttf") format("truetype"); }

@font-face {
  font-family: 'Dosis-Bold';
  src: url("../fonts/Dosis/Dosis-Bold.ttf") format("truetype"); }

.boxCenter {
  position: relative;
  top: 50%;
  transform: translateY(-50%); }

.linearLeft {
  float: left; }

.linearRight {
  float: right; }

.linearContainer {
  overflow: hidden; }

/* clearfix */
.linearContainer:before, .linearContainer:after {
  content: "";
  display: table; }

.linearContainer:after {
  clear: both; }

html, body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0; }

body {
  position: relative;
  background: url("../img/back.jpg");
  background-size: cover;
  color: #000080;
  font-family: Roboto; }

div.contents {
  width: 600px;
  margin: auto;
  padding-top: 120px;
  font-size: 14px; }

div.title-box {
  width: 220px;
  height: 100px;
  margin: 50px auto;
  padding: 50px 100px;
  background-color: #000080;
  border-radius: 5px; }

h1.title {
  margin-top: 29px;
  margin-bottom: 0;
  color: #ffffff;
  text-align: center;
  font-size: 52px;
  font-family: Oswald; }

div.subtitle-box {
  width: 470px;
  margin: 50px auto;
  line-height: 23px;
  text-align: center;
  font-family: Roboto-Thin; }

div.btn-table {
  display: table;
  width: 540px;
  padding-right: 30px;
  padding-left: 30px; }
  div.btn-table div.btn-cell {
    display: table-cell;
    padding: 20px; }

a.btn {
  display: block;
  width: 140px;
  height: 22px;
  margin: auto;
  padding: 9px 10px;
  border-radius: 3px;
  text-align: center;
  background-color: #64dd17;
  text-decoration: none; }
  a.btn span {
    height: 22px;
    display: inline-block;
    vertical-align: middle;
    color: #ffffff;
    font-size: 13px;
    letter-spacing: 0.06em;
    line-height: 22px; }



